.contacts {
  position: relative;
  padding: 26px 0 54px 0;
}
.contacts__list {
  position: relative;
  display: flex;
  row-gap: 30px;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  padding: 32px 250px 54px 30px;
  background-color: #F2FCFF;
  border-radius: 20px;
}
@media only screen and (max-width: 1100px) {
  .contacts__list {
    row-gap: 30px;
    padding: 30px;
  }
}
@media only screen and (max-width: 700px) {
  .contacts__list {
    row-gap: 20px;
    padding: 20px;
  }
}
.contacts__item {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-right: 10px;
  width: 48%;
}
.contacts__item--rec {
  position: absolute;
  height: 100%;
  width: 179px;
  top: 30px;
  right: 60px;
}
@media only screen and (max-width: 1100px) {
  .contacts__item {
    margin-right: 0;
  }
  .contacts__item--rec {
    position: relative;
    top: auto;
    right: auto;
  }
}
@media only screen and (max-width: 700px) {
  .contacts__item {
    width: 100%;
  }
}
.contacts__name {
  font-style: 24px;
  line-height: 33px;
  font-weight: 600;
  text-transform: uppercase;
}
@media only screen and (max-width: 700px) {
  .contacts__name {
    font-style: 18px;
    line-height: 33px;
  }
}
.contacts__text, .contacts__address {
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
}
.contacts__tel-list, .contacts__hours-list, .contacts__mail-list {
  display: flex;
  flex-direction: column;
}
.contacts__tel-list a, .contacts__hours-list a, .contacts__mail-list a {
  font-weight: 600;
  font-size: 18px;
  line-height: 29px;
  color: #214250;
}
.contacts__tel-list a span, .contacts__hours-list a span, .contacts__mail-list a span {
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
  color: #214250;
}
@media only screen and (max-width: 700px) {
  .contacts__tel-list a, .contacts__hours-list a, .contacts__mail-list a {
    font-size: 16px;
    line-height: 24px;
  }
  .contacts__tel-list a span, .contacts__hours-list a span, .contacts__mail-list a span {
    font-size: 12px;
    line-height: 20px;
  }
}
.contacts__mail-list {
  gap: 8px;
}
.contacts__mail-list span {
  font-size: 14px;
  line-height: 22px;
}
.contacts__logo {
  z-index: -1;
  position: absolute;
  bottom: 0;
  left: 50%;
  pointer-events: none;
}