.breadcrumbs {
  margin-top: 20px;
}
@media (max-width: 769px) {
  .breadcrumbs {
    display: none;
  }
}
.breadcrumbs a {
  font-size: 14px;
  line-height: 20px;
  color: #214250;
}
.breadcrumbs ol {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  width: max-content;
  font-size: 14px;
  line-height: 20px;
  color: #214250;
}
.breadcrumbs ol li:not(:last-child) {
  margin-right: 17px;
}
.breadcrumbs .span_m_l_20 {
  margin-left: 15px;
}