.pager {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 30px;
}
.pager__number {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 44px;
  height: 55px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
  color: #214250;
  transition: opacity 0.5s;
}
@media (hover: hover) {
  .pager__number.current:hover, .pager__number.disabled:hover {
    opacity: 1;
  }
  .pager__number:hover {
    opacity: 0.5;
  }
}
@media (max-width: 399px) {
  .pager__number {
    padding: 5px;
    min-width: 28px;
    height: 33px;
  }
}
.pager .current {
  color: #66A316;
  pointer-events: none;
}
.pager .disabled {
  opacity: 0.5;
  pointer-events: none;
}
.pager__icon {
  border: solid #6A6BBF;
  border-width: 0 2px 2px 0;
  padding: 2px;
  border-radius: 0;
  display: inline-block;
  min-width: 10px;
  height: 10px;
  transform: rotate(-45deg);
  margin-right: 2px;
}
.pager__next, .pager__prev {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 0px 4px 6px 0px rgba(223, 223, 223, 0.6);
  transition: box-shadow 0.3s;
}
@media (hover: hover) {
  .pager__next:hover, .pager__prev:hover {
    box-shadow: 0px 4px 6px 0px rgba(112, 112, 112, 0.6);
  }
}
.pager__next {
  margin-left: auto;
}
.pager__prev {
  margin-right: auto;
}
.pager__prev .pager__icon {
  transform: rotate(135deg);
  margin-left: 2px;
  margin-right: 0;
}