@charset "UTF-8";
.tabs {
  width: 100%;
}
.tabs__controls {
  display: flex;
  overflow: hidden;
}
.tabs__button {
  position: relative;
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  color: #214250;
  border-radius: 16px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  padding: 16px 36px;
  transition: background-color 0.3s, opacity 0.3s;
}
.tabs__button--left::after, .tabs__button--right::after {
  content: "";
  display: block;
  position: absolute;
  z-index: 4;
  width: 16px;
  height: 30px;
  background-color: transparent;
  border-radius: 0 0 0 16px;
  box-shadow: -10px 10px 0 10px #F7F8F5;
  bottom: -1px;
  right: -16px;
  transition: opacity 0.3s;
  opacity: 0;
}
.tabs__button--right::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 4;
  width: 16px;
  height: 30px;
  background-color: transparent;
  border-radius: 0 0 0 16px;
  box-shadow: -10px 10px 0 10px #F7F8F5;
  bottom: -1px;
  left: -16px;
  transition: opacity 0.3s;
  opacity: 0;
  transform: scale(-1, 1);
}
.tabs__button.is-active {
  background-color: #F7F8F5;
  pointer-events: none;
}
.tabs__button.is-active::before {
  opacity: 1;
}
.tabs__button.is-active::after {
  opacity: 1;
}
@media (hover: hover) {
  .tabs__button:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 769px) {
  .tabs__button {
    width: 50%;
    font-size: 14px;
    line-height: 20px;
    padding: 16px 20px;
  }
  .tabs__button--right::after {
    display: none;
  }
}
.tabs__box {
  position: relative;
}
.tabs__item {
  position: relative;
  height: 0;
  opacity: 0;
  padding: 0;
  visibility: hidden;
  overflow: hidden;
  transition: background-color 0.3s;
}
.tabs__item.is-active {
  height: auto;
  width: 100%;
  opacity: 1;
  visibility: visible;
  overflow: visible;
  background-color: #F7F8F5;
}
.tabs__item--info {
  border-radius: 16px;
  border-top-left-radius: 0;
}
.tabs__item--info h2 {
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  text-align: left;
  color: #214250;
  margin-bottom: 12px;
}
.tabs__item--info p {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
}
.tabs__item--info ul, .tabs__item--info ol {
  padding: 0;
  margin-left: 20px;
}
.tabs__item--info ul li, .tabs__item--info ol li {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
}
.tabs__item--info ul:last-child, .tabs__item--info ol:last-child {
  list-style: none;
  margin: 0;
}
.tabs__item--info ul:last-child li, .tabs__item--info ol:last-child li {
  margin-bottom: 1rem;
}
.tabs__item--info ul:last-child li::before, .tabs__item--info ol:last-child li::before {
  content: attr(data-counter) ")";
  display: inline-block;
  width: 16px;
  text-align: right;
  margin-right: 8px;
}
.tabs__item--info ul:last-child li:nth-child(1)::before, .tabs__item--info ol:last-child li:nth-child(1)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(2)::before, .tabs__item--info ol:last-child li:nth-child(2)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(3)::before, .tabs__item--info ol:last-child li:nth-child(3)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(4)::before, .tabs__item--info ol:last-child li:nth-child(4)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(5)::before, .tabs__item--info ol:last-child li:nth-child(5)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(6)::before, .tabs__item--info ol:last-child li:nth-child(6)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(7)::before, .tabs__item--info ol:last-child li:nth-child(7)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(8)::before, .tabs__item--info ol:last-child li:nth-child(8)::before {
  content: ")";
}
.tabs__item--info ul:last-child li:nth-child(9)::before, .tabs__item--info ol:last-child li:nth-child(9)::before {
  content: ")";
}
.tabs__item--info ol li {
  margin-bottom: 1rem;
}
.tabs__item--info ol li:last-child {
  margin-bottom: 3rem;
}
.tabs__item--info figure {
  max-width: 70%;
  margin: 0 0 38px 0;
}
.tabs__item--info figure img {
  width: 100%;
  object-fit: cover;
}
@media only screen and (max-width: 769px) {
  .tabs__item--info figure {
    max-width: 100%;
    width: 100%;
    margin: 0 0 24px 0;
  }
}
.tabs__item--info figcaption {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  color: #214250;
  margin-bottom: 14px;
}
.tabs__item--table {
  border-radius: 16px;
}
@media only screen and (max-width: 769px) {
  .tabs__item--table {
    border-top-right-radius: 0;
  }
}
.tabs__item--table h2 {
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  text-align: left;
  margin-bottom: 15px;
}
.tabs__item--table table {
  width: 100%;
  border-collapse: collapse;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}
@media only screen and (max-width: 1200px) {
  .tabs__item--table table {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
  }
}
@media only screen and (max-width: 769px) {
  .tabs__item--table table {
    min-width: 800px;
  }
}
.tabs__item--table table th, .tabs__item--table table td {
  border: 1px solid #C4C4C4;
  padding: 10px;
}
@media only screen and (max-width: 1200px) {
  .tabs__item--table table th, .tabs__item--table table td {
    padding: 4px;
  }
}
.tabs__item--table table td {
  text-align: center;
}
.tabs__item--table table th {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  font-weight: 400;
  word-break: normal;
  hyphens: auto;
}
.tabs__item--table table td[rowspan="5"] {
  background-color: #F7F8F5;
}
.tabs__item--table table tbody tr:nth-child(odd) {
  background-color: #ffffff;
}

.tabs__item--info.is-active {
  padding: 30px;
}

.tabs__item--table.is-active {
  padding: 25px 14px 30px 14px;
}
@media only screen and (max-width: 1000px) {
  .tabs__item--table.is-active {
    overflow-x: scroll;
  }
}