.catalog {
  position: relative;
  padding: 26px 0 66px 0;
}
.catalog__content {
  position: relative;
}
@media only screen and (max-width: 1100px) {
  .catalog__content {
    gap: 16px;
    display: flex;
    flex-direction: column;
  }
}
.catalog aside {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-self: flex-start;
  width: 333px;
  padding: 24px 20px;
  background-color: #F2FCFF;
  border-radius: 10px;
}
.catalog aside ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.catalog aside ul li {
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  text-align: left;
  text-transform: uppercase;
}
.catalog aside ul li:not(:last-child) {
  padding-bottom: 16px;
  border-bottom: 1px solid #EBEBEB;
}
.catalog aside ul li a {
  color: #214250;
  transition: opacity 0.3s;
}
.catalog aside ul li a.is-current {
  color: #518311;
  pointer-events: none;
}
@media (hover: hover) {
  .catalog aside ul li a:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 1100px) {
  .catalog aside {
    position: relative;
    gap: 16px;
    width: 458px;
    padding: 20px;
    width: 100%;
  }
  .catalog aside h2 {
    font-size: 18px;
    line-height: 26px;
  }
  .catalog aside ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  .catalog aside ul li {
    font-size: 18px;
    line-height: 30px;
  }
  .catalog aside ul li a {
    color: #214250;
    transition: opacity 0.3s;
  }
}
@media only screen and (max-width: 1100px) and (hover: hover) {
  .catalog aside ul li a:hover {
    opacity: 0.7;
  }
}
.catalog__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  min-height: 100vh;
  width: calc(100% - 360px);
  margin-left: auto;
}
.catalog__list--card {
  flex-direction: column;
  gap: 26px;
}
@media only screen and (max-width: 1100px) {
  .catalog__list {
    width: 100%;
  }
  .catalog__list--card {
    margin-top: 30px;
  }
}
.catalog__item {
  width: 31%;
  flex-grow: 1;
  background-color: #F7F8F5;
  padding: 18px 18px 32px 18px;
  border-radius: 20px;
  transition: box-shadow 0.3s;
}
.catalog__item-name {
  font-size: 18px;
  font-weight: 600;
  line-height: 25.2px;
  text-align: left;
  margin-bottom: 12px;
  color: #214250;
  transition: opacity 0.3s;
}
.catalog__item-description {
  color: #214250;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  transition: opacity 0.3s;
}
.catalog__item-img {
  position: relative;
  width: 100%;
  height: 265px;
  background-color: #ffffff;
  border-radius: 20px;
  margin-bottom: 20px;
  overflow: hidden;
}
.catalog__item-img img {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  object-fit: contain;
  transition: width 0.3s, height 0.3s;
}
@media only screen and (max-width: 796px) {
  .catalog__item {
    width: 100%;
  }
}
@media (hover: hover) {
  .catalog__item:hover {
    box-shadow: 0px 4px 6px 0px rgba(112, 112, 112, 0.6);
  }
  .catalog__item:hover .catalog__item-name {
    opacity: 0.7;
  }
  .catalog__item:hover .catalog__item-description {
    opacity: 0.7;
  }
  .catalog__item:hover .catalog__item-img img {
    width: 105%;
    height: 105%;
  }
}
.catalog__card-header {
  display: flex;
  gap: 36px;
  margin-bottom: 53px;
}
@media only screen and (max-width: 1200px) {
  .catalog__card-header {
    flex-direction: column;
    margin-bottom: 30px;
  }
}
.catalog__card-img {
  position: relative;
  flex-shrink: 0;
  width: 42%;
  min-height: 380px;
  border-radius: 16px;
  border: 1px solid #EBEBEB;
}
.catalog__card-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  object-fit: contain;
}
@media only screen and (max-width: 1500px) {
  .catalog__card-img {
    width: 30%;
  }
}
@media only screen and (max-width: 1200px) {
  .catalog__card-img {
    width: 100%;
  }
}
.catalog__card-description {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.catalog__card-description h2 {
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  text-align: left;
}
.catalog__card-description P {
  font-size: 14px;
  line-height: 20px;
  text-align: left;
  margin: 0;
}
.catalog__card-controls {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  gap: 18px;
  margin-top: auto;
}
.catalog__card-controls .button {
  margin: 0;
}
@media only screen and (max-width: 1200px) {
  .catalog__card-controls {
    margin-top: 30px;
    justify-content: center;
  }
  .catalog__card-controls .button {
    flex-grow: 1;
  }
}
@media only screen and (max-width: 769px) {
  .catalog__card-controls {
    flex-wrap: wrap;
  }
  .catalog__card-controls .button {
    width: 100%;
  }
}
.catalog h1 {
  font-size: 40px;
  line-height: 56px;
  font-weight: 700;
  margin-bottom: 40px;
}
@media only screen and (max-width: 769px) {
  .catalog h1 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 796px) {
  .catalog__content--card aside {
    display: none;
  }
}