.button {
  display: inline-flex;
  align-items: center;
  position: relative;
  text-align: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
  font-family: Onest;
  font-size: 14px;
  font-weight: 600;
  line-height: 17.85px;
  color: #667884;
  background-color: rgba(102, 120, 132, 0.3215686275);
  padding: 20px 40px;
  border-radius: 64px;
  border: none;
}
@media (hover: hover) {
  .button:hover {
    background-color: rgba(0, 0, 0, 0.3215686275);
    color: #ffffff;
  }
}