.header {
  background-color: transparent;
  z-index: 99;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  transition: box-shadow 0.3s, height 0.3s, transform 0.3s;
}
.header__header {
  display: flex;
  align-items: center;
  height: 50px;
  width: 100%;
  padding: 0 20px;
  background-color: #2E3237;
}
.header__location {
  display: flex;
  flex-shrink: 0;
  gap: 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
}
.header__location span:first-child {
  color: rgba(243, 240, 226, 0.4196078431);
}
.header__location span:nth-child(2) {
  color: rgba(243, 240, 226, 0.8588235294);
  cursor: pointer;
}
.header__info {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 45%;
  justify-content: space-between;
  margin: 0 auto;
}
.header__info-item {
  flex-shrink: 0;
  margin-right: 12px;
}
.header__info-link {
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: #9B9B9B;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__info-link:hover {
    opacity: 0.8;
  }
}
.header__first-mail {
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: #9B9B9B;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__first-mail:hover {
    opacity: 0.8;
  }
}
.header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 36px 0;
  align-items: center;
}
@media only screen and (max-width: 1199px) {
  .header__wrapper {
    justify-content: space-between;
  }
}
@media only screen and (max-width: 769px) {
  .header__wrapper {
    padding: 20px 0;
    align-items: center;
  }
}
.header__logo {
  display: flex;
  width: 220.64px;
  height: 37px;
  flex-shrink: 0;
  margin-right: auto;
}
.header__logo img {
  position: relative;
  flex-shrink: 0;
  width: 220.64px;
  height: 37px;
}
.header__contacts {
  display: flex;
  margin-right: 24px;
  justify-content: space-between;
}
@media only screen and (max-width: 1199px) {
  .header__contacts {
    margin-left: 0;
  }
}
@media only screen and (max-width: 769px) {
  .header__contacts {
    display: none;
  }
}
.header__tel {
  display: flex;
  align-items: center;
  gap: 30px;
}
.header__tel-link {
  font-size: 18px;
  color: #2E3237;
  font-weight: 600;
  line-height: 22.95px;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__tel-link:hover {
    opacity: 0.8;
  }
}
.header__tel-list {
  display: flex;
  flex-direction: column;
}
.header__mobile-contacts {
  display: none;
  margin-top: 30px;
  padding-bottom: 30px;
}
.header__mobile-contacts .header__mail {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 20px;
  margin-right: 0;
}
.header__mobile-contacts .header__mail a {
  font-weight: 600;
  color: #214250;
  text-align: center;
}
.header__mobile-contacts .header__mail span {
  color: #ffffff;
}
@media only screen and (max-width: 769px) {
  .header__mobile-contacts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.header__nav {
  width: 100%;
  padding: 28px 0;
  position: relative;
  transition: opacity 0.3s, transform 0.3s, margin 0.3s;
}
.header__nav-list {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  justify-content: space-between;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}
.header__nav-item {
  flex-shrink: 0;
  padding: 0 60px;
  border-left: 1px solid rgba(46, 50, 55, 0.1607843137);
  border-right: 1px solid rgba(46, 50, 55, 0.1607843137);
}
.header__nav-item:first-child {
  padding-left: 0;
  border-left: none;
}
.header__nav-item:last-child {
  padding-right: 0;
  border-right: none;
}
.header__nav-link {
  flex-shrink: 0;
  font-family: Optima;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  color: #2E3237;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__nav-link:hover {
    opacity: 0.8;
  }
}
.header__burger {
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 46px;
  height: 46px;
  border-radius: 6px;
  background-color: #66A316;
  flex-shrink: 0;
  padding: 8px;
  position: relative;
  z-index: 3;
  cursor: pointer;
  transition: background-color 0.3s;
}
.header__burger i {
  display: block;
  height: 3px;
  width: 100%;
  border-radius: 2px;
  background: #fff;
  left: 0;
  top: 0;
  margin-bottom: 6px;
}
.header__burger i:first-child {
  width: 50%;
}
.header__burger i:last-child {
  margin-bottom: 0;
}
@media (hover: hover) {
  .header__burger:hover {
    background-color: #588d13;
  }
}
.header .search {
  position: relative;
  width: 378px;
  border-radius: 64px;
  overflow: hidden;
  margin: 0 auto 0 24px;
}
.header .search button {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  height: 100%;
  width: 60px;
}
.header .search span {
  display: block;
  height: 15px;
  width: 15px;
}
.header .search input {
  width: 100%;
  padding: 20px 60px 20px 42px;
  border-radius: 64px;
  overflow: hidden;
  border: 1px solid rgba(46, 50, 55, 0.3215686275);
  font-family: Onest;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: #2E3237;
}
.header .icons {
  display: flex;
  gap: 4px;
}
.header .icons__link {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  background-color: #575A5F;
  border-radius: 50%;
  transition: background-color 0.3s;
}
@media (hover: hover) {
  .header .icons__link:hover {
    background-color: #3a3c3f;
  }
}