.article {
  position: relative;
  padding: 26px 0 66px 0;
}
.article__content {
  position: relative;
}
@media only screen and (max-width: 1100px) {
  .article__content {
    gap: 16px;
    display: flex;
    flex-direction: column;
  }
}
.article aside {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-self: flex-start;
  width: 458px;
  padding: 28px 28px 56px 28px;
  background-color: #F2FCFF;
  border-radius: 10px;
}
.article aside div {
  display: flex;
  gap: 10px;
}
.article aside div span {
  display: inline-block;
  padding: 10px 14px;
  background-color: #ffffff;
  border-radius: 10px;
  font-size: 14px;
  line-height: 20px;
}
.article aside div span i {
  font-style: normal;
}
@media only screen and (max-width: 600px) {
  .article aside div {
    flex-direction: column;
    text-align: center;
  }
}
.article aside h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 34px;
  text-transform: uppercase;
}
.article aside ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.article aside ul li {
  font-size: 18px;
  line-height: 30px;
}
.article aside ul li a {
  color: #214250;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .article aside ul li a:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 1100px) {
  .article aside {
    position: relative;
    gap: 16px;
    width: 458px;
    padding: 20px;
    width: 100%;
  }
  .article aside h2 {
    font-size: 18px;
    line-height: 26px;
  }
  .article aside ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  .article aside ul li {
    font-size: 18px;
    line-height: 30px;
  }
  .article aside ul li a {
    color: #214250;
    transition: opacity 0.3s;
  }
}
@media only screen and (max-width: 1100px) and (hover: hover) {
  .article aside ul li a:hover {
    opacity: 0.7;
  }
}
.article article {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: calc(100% - 494px);
  margin-left: auto;
}
.article article img {
  border-radius: 10px;
}
.article article img:not(:last-child) {
  margin-bottom: 30px;
}
.article article p {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  text-align: left;
  margin: 0;
}
.article article p:not(:last-child) {
  margin-bottom: 30px;
}
.article article h2 {
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  margin-bottom: 30px;
}
@media only screen and (max-width: 1100px) {
  .article article {
    width: 100%;
  }
}
.article h1 {
  font-size: 40px;
  line-height: 56px;
  font-weight: 700;
  margin-bottom: 40px;
}
@media only screen and (max-width: 769px) {
  .article h1 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 20px;
  }
}